<template><section><h1>量算工具</h1>
<ul>
<li><code v-pre>vc-measure-distance</code> 组件用于距离量算。</li>
<li><code v-pre>vc-measure-area</code> 组件用于量算面积</li>
<li><code v-pre>vc-measure-height</code> 组件用于量算高程。</li>
</ul>
<h2>示例</h2>
<h3>测量距离、面积、高度</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3DOnly>
        <vc-measure-distance ref="measureDistance" @activeEvt="activeEvt" @measureEvt="measureEvt"></vc-measure-distance>
        <vc-measure-area ref="measureArea" @activeEvt="activeEvt" @measureEvt="measureEvt" :perPositionHeight="true"></vc-measure-area>
        <vc-measure-height ref="measureHeight" @activeEvt="activeEvt" @measureEvt="measureEvt"></vc-measure-height>
        <vc-primitive-3dtileset :url="modelUrl" @readyPromise="readyPromise"></vc-primitive-3dtileset>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="toggle('measureDistance')">{{ distanceMeasuring ? '停止' : '距离' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureArea')">{{ areaMeasuring ? '停止' : '面积' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureHeight')">{{ heightMeasuring ? '停止' : '高度' }}</md-button>
        <md-button class="md-raised md-accent" @click="clear">清除</md-button>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span> <span class="hljs-attr">scene3DOnly</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-measure-distance</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"measureDistance"</span> <span class="hljs-attr">@activeEvt</span>=<span class="hljs-string">"activeEvt"</span> <span class="hljs-attr">@measureEvt</span>=<span class="hljs-string">"measureEvt"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-measure-distance</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-measure-area</span>
        <span class="hljs-attr">ref</span>=<span class="hljs-string">"measureArea"</span>
        <span class="hljs-attr">@activeEvt</span>=<span class="hljs-string">"activeEvt"</span>
        <span class="hljs-attr">@measureEvt</span>=<span class="hljs-string">"measureEvt"</span>
        <span class="hljs-attr">:perPositionHeight</span>=<span class="hljs-string">"true"</span>
      &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-measure-area</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-measure-height</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"measureHeight"</span> <span class="hljs-attr">@activeEvt</span>=<span class="hljs-string">"activeEvt"</span> <span class="hljs-attr">@measureEvt</span>=<span class="hljs-string">"measureEvt"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-measure-height</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-3dtileset</span> <span class="hljs-attr">:url</span>=<span class="hljs-string">"modelUrl"</span> <span class="hljs-attr">@readyPromise</span>=<span class="hljs-string">"readyPromise"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-3dtileset</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"toggle('measureDistance')"</span>
        &gt;</span>{{ distanceMeasuring ? '停止' : '距离' }}<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>
      &gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"toggle('measureArea')"</span>&gt;</span>{{ areaMeasuring ? '停止' : '面积' }}<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"toggle('measureHeight')"</span>&gt;</span>{{ heightMeasuring ? '停止' : '高度' }}<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">md-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"md-raised md-accent"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"clear"</span>&gt;</span>清除<span class="hljs-tag">&lt;/<span class="hljs-name">md-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">modelUrl</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json'</span>,
        <span class="hljs-attr">distanceMeasuring</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">areaMeasuring</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">heightMeasuring</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
      },
      toggle(type) {
        <span class="hljs-keyword">this</span>.$refs[type].measuring = !<span class="hljs-keyword">this</span>.$refs[type].measuring
      },
      clear() {
        <span class="hljs-keyword">this</span>.$refs.measureDistance.clear()
        <span class="hljs-keyword">this</span>.$refs.measureArea.clear()
        <span class="hljs-keyword">this</span>.$refs.measureHeight.clear()
      },
      activeEvt(_) {
        <span class="hljs-keyword">this</span>[_.type] = _.isActive
      },
      measureEvt(result) {
        <span class="hljs-built_in">console</span>.log(result)
      },
      readyPromise(tileset) {
        <span class="hljs-keyword">const</span> { viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.zoomTo(tileset, <span class="hljs-keyword">new</span> Cesium.HeadingPitchRange(<span class="hljs-number">0.0</span>, <span class="hljs-number">-0.5</span>, tileset.boundingSphere.radius * <span class="hljs-number">2.0</span>))
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<h3>vc-measure-distance</h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>mode</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 测量模式，0 连续测量，1 测量一次就结束。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'100 20px SimSun'</code></td>
<td><code v-pre>optional</code> 指定 label 字体。</td>
</tr>
<tr>
<td>depthTest</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定标签文字和线对象是参与深度测试。</td>
</tr>
</tbody>
</table>
<hr>
<h3>vc-measure-area</h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>perPositionHeight</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 面是否使用每个点的高度，否的话绘制的面是贴地的。（但面积算法还不是算的贴地面积）</td>
</tr>
<tr>
<td>mode</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 测量模式，0 连续测量，1 测量一次就结束。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'100 20px SimSun'</code></td>
<td><code v-pre>optional</code> 指定标签 CSS 字体。</td>
</tr>
<tr>
<td>depthTest</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定标签文字和线对象是参与深度测试。</td>
</tr>
</tbody>
</table>
<hr>
<h3>vc-measure-height</h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>mode</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 测量模式，0 连续测量，1 测量一次就结束。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'100 20px SimSun'</code></td>
<td><code v-pre>optional</code> 指定标签 CSS 字体。</td>
</tr>
<tr>
<td>depthTest</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定标签文字和线对象是参与深度测试。</td>
</tr>
</tbody>
</table>
<hr>
<h3>标签文字属性</h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>backgroundColor</td>
<td>String|Array|Object</td>
<td><code v-pre>'rgba(38, 38, 38, 0.85)'</code></td>
<td><code v-pre>optional</code> 指定标签文字背景颜色。</td>
</tr>
<tr>
<td>fillColor</td>
<td>String|Array|Object</td>
<td><code v-pre>WHITE</code></td>
<td><code v-pre>optional</code> 指定文字标签填充色。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'100 20px SimSun'</code></td>
<td><code v-pre>optional</code> 指定文字标签 CSS 字体。</td>
</tr>
<tr>
<td>labelStyle</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定标签文字填充色。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>String|Array|Object</td>
<td><code v-pre>'BLUE'</code></td>
<td><code v-pre>optional</code> 指定标签文字轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 指定标签文字轮廓线宽度。</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre>{x: 15, y: -20}</code></td>
<td><code v-pre>optional</code> 指定标签文字像素偏移。</td>
</tr>
<tr>
<td>showBackground</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定标签文字是否显示背景。</td>
</tr>
</tbody>
</table>
<h3>线属性</h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>polylineColor</td>
<td>String|Array|Object</td>
<td><code v-pre>'#51ff00'</code></td>
<td><code v-pre>optional</code> 指定线颜色。</td>
</tr>
<tr>
<td>polylineWidth</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定线宽度。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>activeEvt</td>
<td>{ type: String, isActive: Boolean }</td>
<td>量算开始或停止时触发，返回量算类型包括<code v-pre>'areaMeasuring'</code>、<code v-pre>'distanceMeasuring'</code>、<code v-pre>'heightMeasuring'</code>。</td>
</tr>
<tr>
<td>measureEvt</td>
<td>{ polyline: Object, label: Object, type: String, finished: Boolean }</td>
<td>量算过程中触发。返回量算的结果，文字标签对象。可以获取文字标签对象自定义文字单位小数点等。</td>
</tr>
</tbody>
</table>
<h2>方法</h2>
<table>
<thead>
<tr>
<th>方法名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>clear</td>
<td></td>
<td>清除量算结果（同时停止量算）。</td>
</tr>
</tbody>
</table>
<h2>备注</h2>
<ul>
<li>超图版本量算这样设置线和文字标签一直可见</li>
</ul>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
