      <span class="hljs-tag">&lt;<span class="hljs-name">vc-measure-distance</span> <span class="hljs-attr">depthTest</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"measureDistance"</span> <span class="hljs-attr">@activeEvt</span>=<span class="hljs-string">"activeEvt"</span> <span class="hljs-attr">@measureEvt</span>=<span class="hljs-string">"measureEvt"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-measure-distance</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-measure-area</span>
