    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">modelUrl</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json'</span>,
        <span class="hljs-attr">distanceMeasuring</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">areaMeasuring</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">heightMeasuring</span>: <span class="hljs-literal">false</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.measureDistance.$refs.polylineCollection.createPromise,
        <span class="hljs-keyword">this</span>.$refs.measureDistance.$refs.labelCollection.createPromise,
        <span class="hljs-keyword">this</span>.$refs.measureArea.$refs.polylineCollection.createPromise,
        <span class="hljs-keyword">this</span>.$refs.measureArea.$refs.labelCollection.createPromise,
        <span class="hljs-keyword">this</span>.$refs.measureHeight.$refs.polylineCollection.createPromise,
        <span class="hljs-keyword">this</span>.$refs.measureHeight.$refs.labelCollection.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances.reduce(<span class="hljs-function">(<span class="hljs-params">pre, cur</span>) =&gt;</span> {
          <span class="hljs-keyword">const</span> rs = Cesium.RenderState.fromCache({
            <span class="hljs-attr">depthMask</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">depthTest</span>: {
              <span class="hljs-attr">enabled</span>: <span class="hljs-literal">false</span>
            }
          })
          <span class="hljs-keyword">if</span> (cur.cesiumObject <span class="hljs-keyword">instanceof</span> Cesium.PolylineCollection) {
            cur.cesiumObject._opaqueRS = rs
          } <span class="hljs-keyword">else</span> {
            cur.cesiumObject._billboardCollection._depthTestEnable = <span class="hljs-literal">false</span>
            cur.cesiumObject._backgroundBillboardCollection._depthTestEnable = <span class="hljs-literal">false</span>
          }
        }, [])
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
      },
      toggle(type) {
        <span class="hljs-keyword">this</span>.$refs[type].measuring = !<span class="hljs-keyword">this</span>.$refs[type].measuring
      },
      clear() {
        <span class="hljs-keyword">this</span>.$refs.measureDistance.clear()
        <span class="hljs-keyword">this</span>.$refs.measureArea.clear()
        <span class="hljs-keyword">this</span>.$refs.measureHeight.clear()
      },
      activeEvt(_) {
        <span class="hljs-keyword">this</span>[_.type] = _.isActive
      },
      measureEvt(result) {
        <span class="hljs-built_in">console</span>.log(result)
